.section-search {
  height: 50px;
  margin-top: 5px;
  margin-bottom: 10px;
}

.search-form {
  width: 95vw;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  margin: 0 auto;
  box-shadow: 2px 3px 2px 0px rgba(0, 0, 0, 0.4);
  border-radius: 0.25rem;
}

.form-control {
  width: 100%;
  height: 80%;
  display: flex;
  justify-content: center;
}
.form-control input {
  flex: 0 90%;
  background: #f1f5f8;
  padding: 1rem;
  font-size: 1.1rem;
  border-color: transparent;
  border-radius: 0.25rem;
  /**/
  flex: 1;
  font-size: 1rem;
  border: 1px solid rgba(0, 0, 0, 0.2);
}
.search-btn {
  margin-left: 5px;
  flex: 0 8%;
  border-radius: 0.25rem;
  border-color: transparent;
  background-color: #fff;
  cursor: pointer;
  border: 1px solid rgba(0, 0, 0, 0.2);
  flex: 0 0 auto;
  padding: 0.5rem;
  transition: all 0.3s linear;
}
.search-btn:hover {
  background-color: #f1f5f8;
}

@media screen and (min-width: 825px) {
  .search-btn {
    flex: 0 10%;
    padding: 0.5rem;
    font-size: 1rem;
  }
}
