.movie {
  display: flex;
  flex-direction: column;
  border: 1px solid rgba(0, 0, 0, 0.3);
  border-radius: 0.25rem;
  background-color: #f9f9f9;
}
.movie:hover {
  background-color: #f0f0f0;
}
.movie-center {
  flex: 0 0 60%;
  overflow: hidden;
  display: flex;
  justify-content: center;
  align-items: center;
}

.movie-img {
  width: 100%;
  height: 100%;
  object-fit: fill;
}
.movie-footer {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  flex: 1;
}

.movie-footer > h3 {
  padding: 3px;
  flex: 1;
  display: flex;
  font-size: 1.2rem;
  justify-content: center;
  align-items: center;
  text-align: center;
}
.footer {
  display: flex;
  font-size: 1rem;
  justify-content: space-between;
  text-transform: capitalize;
  margin: 0 4px;
}

.btn-details {
  margin-top: 10px;
  padding: 2px;
  display: flex;
  justify-content: center;
  border-radius: 0.25rem;
  cursor: pointer;
  text-transform: capitalize;
  letter-spacing: 0.2rem;
  background-color: #fff;
  color: #000;
  padding: 0.5rem 1rem;
}
.btn-details:hover {
  background-color: #000;
  color: #fff;
  transition: all 0.4s linear;
}
@media screen and (max-width: 575px) {
  .movie-center {
    flex: 1;
  }
  .movie-footer {
    flex: 0 0 30%;
    padding: 0.5rem;
  }
  .movie-footer > h3 {
    font-size: 1.2rem;
  }
}
@media screen and (min-width: 576px) {
  .movie-center {
    flex: 0 0 65%;
  }
}
