.section-home {
  flex: 1;
  display: flex;
  flex-direction: column;
  overflow: hidden;
  margin: 10px 0 0 0;
}
.section-center {
  flex: 10;
  display: flex;
  justify-content: center;
  position: relative;
}
.home-article {
  grid-template-columns: 1fr;
  grid-template-rows: 7fr 1fr;
  width: 65%;
  display: none;
}
.activeSlide {
  display: grid;
  z-index: 999;
}
.lastSlide {
  display: grid;
  position: absolute;
  top: 0;
  top: 50%;
  left: 50%;
  height: 85%;
  transform: translate(-85%, -50%);
  opacity: 0.3;
}
.nextSlide {
  position: absolute;
  display: grid;
  top: 50%;
  right: 50%;
  height: 85%;
  transform: translate(85%, -50%);
  opacity: 0.3;
}
.img-center {
  position: relative;
}
.home-img {
  position: absolute;
  width: 100%;
  height: 100%;
}
.home-title {
  display: flex;
  justify-content: center;
  align-items: center;
  text-align: center;
  font-size: 1rem;
}
.activeSlide {
  border: 1px solid rgba(0, 0, 0, 0.2);
}
.lastSlide .home-title,
.nextSlide .home-title {
  visibility: hidden;
}

.index-container {
  height: 40px;
  display: flex;
  justify-content: space-evenly;
  margin: 5px 0;
}
.index-container button {
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: transparent;
  border: transparent;
  cursor: pointer;
  font-size: 1rem;
}

.index-container .prev-indexbtn,
.index-container .next-indexbtn {
  flex: 0 40px;
}
.index-container .pages-btn {
  flex: 1;
  display: flex;
  justify-content: space-evenly;
  align-items: center;
}
.index-container .index-btn {
  width: 20px;
  height: 20px;
  display: flex;
  justify-content: center;
  align-items: center;
  flex: 1;
}

@media screen and (max-width: 599px) {
  .home-article {
    grid-template-rows: 6fr 1fr;
  }
  .index-container {
    display: none;
  }
}

@media screen and (min-width: 600px) {
  .home-article {
    grid-template-rows: 10fr 2fr;
  }

  .home-title {
    font-size: 1.1rem;
  }
}
@media screen and (min-width: 800px) {
  .section-home {
    width: 95vw;
    margin: 10px auto 0;
  }
  .home-article {
    width: 60%;
  }
  .lastSlide {
    height: 85%;
    transform: translate(-100%, -50%);
  }
  .nextSlide {
    height: 85%;
    transform: translate(100%, -50%);
  }
  .home-title {
    font-size: 1.1rem;
  }
  .index-container {
    height: 45px;
  }
  .index-container button {
    font-size: 1.1rem;
  }
  .index-container .prev-indexbtn,
  .index-container .next-indexbtn {
    flex: 0 50px;
  }

  .index-container .index-btn {
    width: 30px;
    height: 20px;
    flex: 1;
  }
}
@media screen and (min-width: 1100px) {
  .home-article {
    width: 55%;
  }
  .home-title {
    font-size: 1.2rem;
  }
  .index-container button {
    font-size: 1.2rem;
  }
  .index-container .prev-indexbtn,
  .index-container .next-indexbtn {
    flex: 0 60px;
  }

  .index-container .index-btn {
    width: 40px;
    height: 20px;
    flex: 1;
  }
}
